/* src/styles/About.css */
.about-container {
  padding: 40px 20px;
  max-width: 900px;
  margin: 0 auto;
  color: #f1f1f1;
}

.section-title {
  font-size: 32px;
  margin-bottom: 24px;
  text-align: center;
  color: #00ffff;
}

.about-description {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 18px;
  color: #cccccc;
}

.highlight-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
  margin-top: 30px;
}

.card {
  background-color: #1f1f1f;
  border: 1px solid #00ffff30;
  padding: 20px;
  border-radius: 16px;
  box-shadow: 0 0 12px rgba(0, 255, 255, 0.05);
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-6px);
}

.card h3 {
  font-size: 18px;
  margin-bottom: 12px;
  color: #00ffff;
}

.card p {
  font-size: 14px;
  color: #ddd;
}
